              img(src='@/assets/curso/images/tema6/ico_9.svg'  alt='').mb-3
            .col-md-9.mb-4.mb-md-0.ps-3
              p.mb-0 Tambin hay que romper el tema de la educacin aburrida, con numerosos recursos y tcnicas didcticas en las que los participantes se divierten, a la vez que estn recibiendo y asimilando informacin. Hay que tener en cuenta que estamos transmitiendo mensajes, pero de forma ACTIVA y PARTICIPATIVA.
          .row.align-items-star.justify-content-center(titulo="Potenciando capacidades")
            .col-3.col-lg-2.lg-0
              img(src='@/assets/curso/images/tema6/ico_9.svg'  alt='').mb-3
            .col-md-9.mb-4.mb-md-0.ps-3
              p.mb-3 La Educacin Ambiental es una educacin para la accin. La metodologa deber ir enfocada a la participacin. Mediante una metodologa adecuada, se completarn todos los pasos necesarios para lograr un proceso formativo completo:
              p.mb-3 	Informacin
              p.mb-3 	Sensibilizacin / concienciacin
              p.mb-3 	Capacitar para la accin

          .row.align-items-star.justify-content-center(titulo="Qu promover?")
            .col-3.col-lg-2.lg-0
              img(src='@/assets/curso/images/tema6/ico_9.svg'  alt='').mb-3
            .col-md-9.mb-4.mb-md-0.ps-3
              p.mb-0 Fomentar los procesos de aprendizaje grupales, ya que, adems de ampliar el horizonte conceptual, permiten el desarrollo y ejercitacin de valores, hbitos, actitudes de comunicacin, cooperacin y trabajo en equipo. En esa misma medida, exigir coherencia entre los principios de la accin y la actitud de el/la monitor/a o el/la gua intrprete. Hay que evitar contradicciones entre la actuacin y los objetivos, ya que los rasgos que caracterizan a este/a profesional y su intervencin son elementos de referencia para el/la destinatario/a.

          .row.align-items-star.justify-content-center(titulo="A quines involucrar y cmo?")
            .col-3.col-lg-2.lg-0
              img(src='@/assets/curso/images/tema6/ico_9.svg'  alt='').mb-3
            .col-md-9.mb-4.mb-md-0.ps-3
              p.mb-0 Plantear estudios y anlisis de las interacciones de los elementos aprovechando el mtodo cientfico para interpretar y conocer las dinmicas y el funcionamiento global del entorno. Asimismo, realizar enfoques interdisciplinares para integrar los distintos aspectos del medio, y, en consecuencia, utilizar como recurso didctico fundamental el entorno inmediato del destinatario para ilustrar conceptos, procesos y problemticas reales y no abstractas. El aprendizaje debe partir del conocimiento y el anlisis de la propia realidad de las destinatarias y los destinatarios, vinculando los contenidos con la propia experiencia.
              
          .row.align-items-star.justify-content-center(titulo="Secuencia lgica de las acciones")
            .col-3.col-lg-2.lg-0
              img(src='@/assets/curso/images/tema6/ico_9.svg'  alt='').mb-3
            .col-md-9.mb-4.mb-md-0.ps-3
              p.mb-0 Una vez definida la metodologa que vamos a seguir, se deber concretar qu actividades se van a desarrollar. Las actividades son las acciones que se van a llevar a cabo para conseguir los objetivos. Debern estar estructuradas y secuenciadas, es decir, llevar un orden lgico coherente con los objetivos que se hayan propuesto y los contenidos que se hayan decidido para el programa.

      p.mb-5 Para ampliar su conocimiento acerca de la metodologa en un plan de promotora ambiental, y para profundizar en lo dicho hasta este punto, reconozca en el siguiente recurso los tipos de actividades que se requiere implementar para la consecucin de los objetivos planteados en proyectos de educacin ambiental.

    .row.justify-content-center.align-items-star.mb-5
