                      h4.mb-2 Acciones sistematizadas
                      p.mt-2 Son maneras sistematizadas de organizar y desarrollar la actividad de un grupo y se basan en la Teora de la Dinmica de Grupos. Dentro de stas, se encuentran diferentes tipos de tcnicas:
