                      h4.mb-2 Pertinencia de las acciones 
                      p.mb-3 Todas las herramientas que se han citado no son exclusivas de la educacin ambiental, ya que provienen de otras muchas disciplinas dedicadas al estudio de procesos de comunicacin, enseanza, aprendizaje. La EA tan slo toma las ms apropiadas y pertinentes para cada momento y situacin.

    .row.align-items-star.mb-3
      .col-lg-2
        .jumbotron-1.p-2.pe-2.ps-4
          h3.text-white.mb-0 Temporizacin.

    p.mb-3 Se trata de establecer el tiempo que se va a emplear en el desarrollo de cada programa o actividad y la fecha o poca en la que se llevar a cabo. Es muy importante estimar la duracin y las fechas en las que se realizarn las actividades propuestas.

    .row.justify-content-center.align-items-star.mb-3
      .col-lg-12
        .bloque-texto-g.color-custom-1.p-3.p-sm-4.p-md-5.mb-5.d-none.d-lg-block
          .bloque-texto-g__img(data-aos="fade-right")(
            :style="{'background-image': `url(${require('@/assets/curso/images/tema6/img_13.png')})`}")
          .bloque-texto-g__texto.p-5.m-right(data-aos="fade-left")
            p.mb-0  Si el desarrollo del proyecto depende al 100 % de la concesin de una subvencin, se indicar que el plazo de realizacin ser posterior (sealando un plazo de tiempo) a la concesin del proyecto. Ejemplo: dos meses a contar desde la concesin del proyecto. Tambin se deber especificar si el periodo de realizacin abarca parte del ao siguiente a aquel en el que se presenta el proyecto.

    .bloque-texto-a.color-custom-2.p-md-0.mb-5.d-block.d-lg-none
