            p.mb-0  Si el desarrollo del proyecto depende al 100 % de la concesin de una subvencin, se indicar que el plazo de realizacin ser posterior (sealando un plazo de tiempo) a la concesin del proyecto. Ejemplo: dos meses a contar desde la concesin del proyecto. Tambin se deber especificar si el periodo de realizacin abarca parte del ao siguiente a aquel en el que se presenta el proyecto. 
