        .jumbotron-1.p-2.pe-2.ps-4
          h3.text-white.mb-0 Recursos.

    p.mb-3 Una vez decidido todo lo anterior, hay que concretar qu recursos se necesitarn. Es un momento importante, ya que una mala previsin puede provocar que el proyecto fracase. Se debern tener en cuenta:


    .row.justify-content-center.align-items-star
      .col-12.col-lg-10(data-aos="fade-left")
        ImagenInfografica.color-acento-contenido.mb-3
          template(v-slot:imagen)
            figure
            img(src="@/assets/curso/images/tema6/info_2.svg").mb-3
              
          .tarjeta-blanca.p-3(x="16.5%" y="23%" numero="1")
            .row.justify-content-center.align-items-center
              .col-lg-12.col-sm-8.mb-lg-0
                p.mb-2 <b>Recursos humanos:</b>Se trata del equipo tcnico. Este variar en funcin de las necesidades del programa, y hay que tener en cuenta todos los aspectos: coordinador-a, monitores-as, personal sanitario, cocineros-as, conductores-as, etc.


          .tarjeta-blanca.p-3(x="46%" y="64.5%" numero="2")
            .row.justify-content-center.align-items-center
              .col-lg-12.mb-4.col-sm-8.mb-lg-0
                p.mb-2 <b>Recursos materiales:</b> Infraestructuras: aulas, alojamiento, etc. 
               

            
          .tarjeta-blanca.p-3(x="82.5%" y="64.5%" numero="3")
            .row.justify-content-center.align-items-center
              .col-lg-12.mb-4.col-sm-8.mb-lg-0      
                p.mb-3 <b>Material inventariable:</b> Hace referencia a todo lo que se necesita para el desarrollo del proyecto y que no es perecedero, al menos a corto plazo. Ejemplos: sillas, mesas, ordenadores, prismticos, etc.

          .tarjeta-blanca.p-3(x="65.5%" y="40.5%" numero="4")
            .row.justify-content-center.align-items-center
              .col-lg-12.mb-4.col-sm-8.mb-lg-0      
                p.mb-3 <b>Material fungible:</b> Se refiere a todo el material que se va a consumir durante el proyecto. Conviene que est bien calculado; un proyecto no es mejor porque use ms material, lo que importa es que el uso del material sea optimizado. La coherencia es un aspecto fundamental cuando se est desarrollando un programa de EA.

    .row.align-items-star.mb-3
      .col-lg-2
        .jumbotron-1.p-2.pe-2.ps-4
          h3.text-white.mb-0 Evaluacin

    p.mb-3 Se entiende por evaluacin toda aquella accin sistemtica y continuada encaminada a recoger informacin acerca del xito de un programa, la satisfaccin de los y las visitantes al hacer una ruta, la adecuacin de los recursos a los tiempos, as como la valoracin del papel desempeado por el/la monitor/a lo largo de la actividad (Gutirrez, 2000).

    p.mb-3 La evaluacin es una parte fundamental en el proceso y una herramienta muy valiosa para el buen funcionamiento del programa, para lo cual, lo ms adecuado es una evaluacin continuada. Por esto, se podran distinguir tres momentos diferentes en la aplicacin de la evaluacin:

    .row.align-items-center.align-items-center.my-5
      .col-lg-12
        TabsA.color-acento-contenido(data-aos="fade")
          .tarjeta.back-tab.px-5.py-4(titulo="Evaluacin inicial")
            .row.px-2.py-2.justify-content-center
              .col-8.col-lg-2 
                img(src="@/assets/curso/images/tema6/ico_10.svg").mb-3
              .col-lg-10
                h4  Evaluacin inicial 
                p.mb-0 Es previa al comienzo de la actividad o el programa a realizar. De esta forma, se asegura una evaluacin de las necesidades, demandas e intereses, permitiendo as adecuar el programa lo ms posible a los intereses de los y las participantes. Para ello, es necesario un contacto previo con el grupo, o bien el anlisis de evaluaciones anteriores. 
          .tarjeta.back-tab.px-5.py-4(titulo="Evaluacin de proceso")
            .row.px-2.py-2.justify-content-center
              .col-8.col-lg-2 
                img(src="@/assets/curso/images/tema6/ico_11.svg").mb-3
              .col-lg-10
                h4 Evaluacin de proceso
                p.mb-0 Se realiza durante el desarrollo de la actividad. Para ello, conviene tantear un poco las expectativas del grupo en las primeras tomas de contacto, y tambin es conveniente realizar actividades de evaluacin intermedias e ir tanteando el grado de satisfaccin de las y los participantes. En esta fase, la observacin tambin nos puede servir como una herramienta muy eficaz de evaluacin.
          .tarjeta.back-tab.px-5.py-4(titulo="Evaluacin posterior a la actividad ")
            .row.px-2.py-2.justify-content-center
              .col-8.col-lg-2 
                img(src="@/assets/curso/images/tema6/ico_12.svg").mb-3
              .col-lg-10
                h4 Evaluacin posterior a la actividad 
                p.mb-0 Llegado este punto, es el momento de verificar si se han cumplido los objetivos, si se han cumplido las actividades y, por supuesto, la posibilidad de incorporar mejoras e introducir cambios. El soporte que se suele utilizar para esto es la realizacin de encuestas o entrevistas, pero se debe tener en cuenta que los cuestionarios conviene que sean sencillos y directos, buscando la mxima objetividad en las respuestas. De esta forma, procesando los datos y analizando los resultados, se ver qu elementos del programa o la actividad conviene reforzar o potenciar y cules conviene descartar.

    .row.align-items-star.mb-3
      .col-lg-2
        .jumbotron-1.p-2.pe-2.ps-4
          h3.text-white.mb-0 Difusin.

    .row.align-items-star.justify-content-center.mb-5
      .col-12.col-lg-6(data-aos="fade-left").mb-3
          img(src='@/assets/curso/images/tema6/img_14.png')
      .col-lg-6
        .jumbotron-8.h-100.p-4
            p.mb-3 La difusin es una parte de todo programa educativo que no debe ser olvidada. Se trata de dar a conocer lo que se hace, ms all de las personas a las que va enfocado directamente el programa. La difusin permite el intercambio de informacin y experiencias, la mejora de programas, la captacin de nuevas personas dispuestas a colaborar o, incluso, nuevos encargos de trabajo.

            p.mb-0 Explore el Anexo Difusin de un proyecto de educacin ambiental y estudie atentamente todos los elementos que all se presentan. 
    
    .row.align-items-star.mb-3
      .col-lg-2
        .jumbotron-1.p-2.pe-2.ps-4
          h3.text-white.mb-0 Presupuesto.

    .row.align-items-center.justify-content-center.mb-3
      .col-lg-6(data-aos="fade-right")
        p.mb-3 Conviene realizar un presupuesto ajustado a los intereses del programa y recordar que no es mejor proyecto el que tenga ms presupuesto. 

        p.mb-3 Muchas veces, se realizan proyectos con muy poco presupuesto que son muy ingeniosos y efectivos. A continuacin, se ofrece un modelo de presupuesto que puede servir como gua.
        .jumbotron-5.p-4
            p.mb-3 Ample su comprensin sobre Presupuesto en un proyecto de educacin ambiental, visualizando el contenido del Anexo Tabla. 3 modelo de presupuesto.      
      .col-12.col-lg-6(data-aos="fade-left").mb-3
        img(src='@/assets/curso/images/tema6/img_15.png')

    Separador

    #t_6_3.titulo-segundo
      .h2 6.3 Recoleccin de la informacin

    p.mb-3 La seleccin y elaboracin de instrumentos de recoleccin de informacin es fundamental en el proceso de trabajo comunitario, es el elemento que rene toda la informacin previa de una investigacin, dando directrices y siendo el marco de referencia para el planteamiento de acciones con la comunidad.

    p.mb-3 Dentro de los instrumentos bsicos para la recoleccin de informacin, se encuentran, segn Cerda (1991), que es uno de los investigadores ms reconocidos a nivel mundial y cuyas premisas siguen vigentes, los siguientes:

    .row.align-items-center.justify-content-center.mb-5(data-aos="zoom-in-down")
      .col-md-4.col-lg.mb-5.mb-lg-0
        .tarjeta-avatar
          img(src='@/assets/curso/images/tema6/ico_13.svg' alt='', style="width:120px; display:block; margin:auto 0 ;")
          .tarjeta.color-custom.m-left
            .p-4
              p.mb-0.text-center   Observacin 

      .col-md-4.col-lg.mb-5.mb-lg-0
        .tarjeta-avatar
          img(src='@/assets/curso/images/tema6/ico_14.svg' alt='', style="width:120px; display:block; margin:auto 0 ;")

          .tarjeta.color-custom.m-left
            .p-3
              p.mb-0.text-center   Entrevista

      .col-md-4.col-lg.mb-5.mb-lg-0
        .tarjeta-avatar
          img(src='@/assets/curso/images/tema6/ico_15.svg' alt='', style="width:120px; display:block; margin:auto 0 ;")
          .tarjeta.color-custom.m-left
            .p-4
              p.mb-0.text-center  Encuesta

    .row.align-items-star.mb-5
      .col-lg-2
        .jumbotron-1.p-2.pe-2.ps-4
          h3.text-white.mb-0 Observacin

    
    .row.align-items-star.justify-content-center.mb-3
      .col-12.col-lg-6(data-aos="fade-left").mb-3
        img(src='@/assets/curso/images/tema6/img_16.png')
      .col-lg-6(data-aos="fade-right")
        p.mb-3 Es el medio preferido de los investigadores sociales, es un mtodo muy fcil de aplicar, directo, y que exige una tabulacin muy sencilla. El observador fija su atencin en una finalidad de la que se tiene clara conciencia, la cual le proporciona la justa postura frente al objeto de observacin. El proceso de observacin exige tener un plan o por lo menos algunas directrices determinadas en relacin con lo que se quiere o espera observar. 
        .jumbotron-5.p-4
            p.mb-3 Uno de los problemas ms complejos y difciles de resolver es, sin lugar a duda, el registro de las observaciones, frente al cual existen posiciones muy encontradas. Se han sugerido las tcnicas y los mtodos ms dispares para alcanzar un ptimo registro" de las observaciones, los cuales van desde rgidos protocolos, utilizados en los experimentos y en las observaciones clnicas, hasta las modalidades ms libres y asistemticas (p. 239).

    .row.align-items-star.mb-5
      .col-lg-2
        .jumbotron-1.p-2.pe-2.ps-4
          h3.text-white.mb-0 Entrevista.

    p.mb-5 Es la ms utilizada en investigaciones cualitativas. Se puede encontrar bajo la denominacin de interrogatorio o de cuestionarios (siendo este ltimo la misma modalidad, pero escrita), se basa en la realizacin de preguntas orientadas hacia la bsqueda de informacin de un objetivo especfico. 

    .row.justify-content-center.mb-5
      .col-lg-3.lg-0
        .tarjeta.tarjeta_grey-2.p-4.h-100  
          .tarjeta
            .row.justify-content-center
              img(src='@/assets/curso/images/tema6/ico_16.svg' alt='imagen relacionada' style='width:150px')
            .p-4 
              h3.text-center Complemento de la observacin
              p.mb-0 Se afirma que, por medio de la entrevista, se obtiene toda aquella informacin que no se logra por la observacin.
      .col-lg-3.lg-0
        .tarjeta.tarjeta_grey-3.p-4.h-100  
          .tarjeta
            .row.justify-content-center
              img(src='@/assets/curso/images/tema6/ico_17.svg' alt='imagen relacionada' style='width:150px')
            .p-4
              h3 Complemento de la observacin
              p.mb-0 De ello se deduce que la entrevista no es otra cosa que una conversacin entre dos personas, una de las cuales se denomina entrevistador y la otra entrevistado. 

      .col-lg-3.lg-0
        .tarjeta.tarjeta_grey-4.p-4.h-100  
          .tarjeta
            .row.justify-content-center
              img(src='@/assets/curso/images/tema6/ico_18.svg' alt='imagen relacionada' style='width:150px')
            .p-4
              h3 Complemento de la observacin
              p.mb-0  Estas dos personas dialogan y conversan de acuerdo con pautas acordadas previamente, o sea, se presupone que para realizar una entrevista debe existir una interaccin verbal entre dos personas dentro de un proceso de accin recproca (Cerda, 1991, p. 259).
      .col-lg-3.lg-0
        .tarjeta.tarjeta_grey-5.p-4.h-100  
          .tarjeta 
            .row.justify-content-center
              img(src='@/assets/curso/images/tema6/ico_19.svg' alt='imagen relacionada' style='width:150px')
            .p-4  
              h3 Complemento de la observacin
              p.mb-0 Dentro las subclasificaciones de este mtodo, se encuentran: 
              ul.lista-ul
                li.mb-0  
                  i.fas.fa-check.c-check
                  p.mb-0 Entrevistas focalizadas

                li.mb-0  
                  i.fas.fa-check.c-check
                  p.mb-0  Entrevistas clnicas

                li.mb-0  
                  i.fas.fa-check.c-check
                  p.mb-0  Entrevistas no dirigidas
        
    .row.align-items-star.mb-5
      .col-lg-2
        .jumbotron-1.p-2.pe-2.ps-4
          h3.text-white.mb-0 Encuesta.

    p.mb-3 En la actualidad, este es uno de los ms usados por empresas de diferentes ndoles o por entidades pblicas. Se define, de acuerdo con Cerda (1991), como la recoleccin sistemtica de datos en una poblacin o en una muestra de la poblacin, mediante el uso de entrevistas personales y otros instrumentos para obtener datos. Habitualmente, a este tipo de estudio se le denomina as cuando se ocupa de grupos de personas numerosos y dispersos (p. 277).

    .row.justify-content-center.align-items-star
      .col-lg-7(data-aos="fade-right")
        AcordionA(data-aos="fade-down-right").mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--gris")
          .row.align-items-star.justify-content-center(titulo="Encuestas abiertas")
            .col-3.col-lg-2.lg-0
