    p.mb-3 Los planes de accin representan la ejecucin de las actividades que propenden por la realizacin de las decisiones que se han tomado para ejecutar las metas. Sin embargo, es importante definir el concepto. Segn Pareja, del portal @cero, Un plan de accin es una hoja de ruta que traza la planificacin de una organizacin para gestionar y controlar tareas, con el fin de cumplir con los objetivos de un proyecto o negocio. Es una gua para definir las metas, fijar plazos y calcular recursos para planificar de forma correcta, optimizar la gestin y mejorar el rendimiento.

